import React, { Component } from 'react';
import styled from 'styled-components';
import '../../assets/main.css';
const Button = styled.button`
  background: yellow;
  font-size: 2rem;
  margin-left: 45vw;
  margin-top: 2rem;
  border: none;
  border-radius: 1rem;
  width: 10rem;
  color: #123456;
`;
const Text = styled.p`
  font-size: 2rem;
  margin-left: 45vw;
  padding-top: 40vh;
  color: white;
`;
export default class Hex extends Component {
  constructor(props) {
    super(props);
    this.state = {
      ColorHolder: '#123456'
    };
  }

  ChangeColorFunction = () => {
    let ColorCode =
      '#' +
      Math.random()
        .toString(16)
        .slice(2, 8)
        .toUpperCase();
    this.setState({
      ColorHolder: ColorCode
    });
  };
  render() {
    const Background = styled.div`
      background-color: ${this.state.ColorHolder};
      height: 100vh;
    `;
    return (
      <Background>
        <Text>Color Hex {this.state.ColorHolder}</Text>
        <Button onClick={this.ChangeColorFunction}>Hex</Button>
      </Background>
    );
  }
}
