import React, { useState } from 'react';
import '../../assets/main.css';
import { useSpring, animated } from 'react-spring';
import { useForm } from 'react-hook-form';
export default function Todolist() {
  const [add, setAdd] = useState([]);
  const { register, handleSubmit, errors } = useForm();

  const onSubmit = data => {
    console.log(data.exampleRequired);
  };

  // console.log(watch('example'));
  const moveLeft = useSpring({
    opacity: 1,
    marginLeft: 0,
    marginTop: 0,
    from: { opacity: 0, marginLeft: 1000, marginTop: 1000 },
    config: { duration: 1000 }
  });
  const handler = () => {
    setAdd(document.getElementById('input').value);
    document.getElementById('input').value = '';
  };
  return (
    <animated.div style={moveLeft}>
      <div>
        <h1 className='text-center mt-8 text-3xl'>Todolist</h1>
        <div className='container mx-auto w-1/3 h-screen'>
          <form onSubmit={handleSubmit(onSubmit)}>
            <input
              type='text'
              name='exampleRequired'
              ref={register({ required: true, minLength: 1 })}
              id='input'
              // onChange={e => setAdd(e.target.value)}
              className='shadow appearance-none border border-green-500 rounded  py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline w-2/3'
              placeholder='Add item'
            />
            <button
              onClick={handler}
              className='bg-green-600 p-2 rounded ml-2 text-white hover:bg-green-300'
            >
              Add item
            </button>
            {errors.exampleRequired && (
              <p className='text-red-500'>This field is required</p>
            )}
            {/* <div>
              {add.map(adds => {
                return <h3>{adds}</h3>;
              })}
            </div> */}
            <div>{add}</div>
          </form>
        </div>
      </div>
    </animated.div>
  );
}
