import React, { Component } from 'react';
import styled from 'styled-components';
import { Input } from './Input';

export default class Form extends Component {
  constructor(props) {
    super(props);
    this.state = {
      ColorHolder: '#123456'
    };
  }

  ChangeColorFunction = () => {
    let ColorCode =
      'rgb(' +
      Math.floor(Math.random() * 256) +
      ',' +
      Math.floor(Math.random() * 256) +
      ',' +
      Math.floor(Math.random() * 256) +
      ')';
    this.setState({
      ColorHolder: ColorCode
    });
  };
  render() {
    const RandomContainer = styled.div`
      text-align: center;
      background-color: ${this.state.ColorHolder};
      padding-top: 10rem;
      height: 100vh;
    `;
    const Button = styled.button`
      min-height: 4rem;
      background-color: transparent;
      border: 1px solid white;
      border-radius: 2rem;
      font-size: 2rem;
      color: white;
      margin: auto;
    `;
    const FormContainer = styled.div`
      margin-left: auto;
      margin-right: auto;
      max-height: 40rem;
      max-width: 40rem;
      background-color: lightblue;
      border-radius: 2rem;
      display: flex;
      flex-direction: column;
      padding: 2rem;
    `;

    return (
      <RandomContainer>
        <FormContainer>
          <Input />
          <Button onClick={this.ChangeColorFunction}>
            Change Background Color
          </Button>
        </FormContainer>
      </RandomContainer>
    );
  }
}
