import React, { Component } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faHeart,
  faImage,
  faThumbsUp
} from '@fortawesome/free-solid-svg-icons';
import { faInstagram } from '@fortawesome/free-brands-svg-icons';
import Fade from './spring/Fade';
import Card from './spring/Card';
import '../App.scss';

export default class Home extends Component {
  state = {
    isLoading: true,
    users: [],
    error: null
  };

  fetchUsers() {
    fetch(
      `https://api.unsplash.com/photos/?client_id=cf49c08b444ff4cb9e4d126b7e9f7513ba1ee58de7906e4360afc1a33d1bf4c0`
    )
      .then(response => response.json())
      .then(data =>
        this.setState({
          users: data,
          isLoading: false
        })
      )
      .catch(error => this.setState({ error, isLoading: false }));
  }

  componentDidMount() {
    this.fetchUsers();
  }
  render() {
    const { isLoading, users, error } = this.state;
    return (
      <div>
        {error ? <p>{error.message}</p> : null}
        {!isLoading ? (
          users.map(user => {
            const { id, urls } = user;
            return (
              <div className='containerFull'>
                <div className='containerHome'>
                  <div key={id} className='imageGallery'>
                    <Card src={urls.small} alt='image' />
                  </div>
                  <div className='containerTwo'>
                    <h3>
                      Total <FontAwesomeIcon icon={faInstagram} />
                    </h3>

                    <h3>
                      <Fade icon={faHeart} className='like' />
                    </h3>
                    <h3>{user.user.total_likes}</h3>
                    <h3>
                      <FontAwesomeIcon icon={faImage} className='photoIcon' />
                    </h3>
                    <h3>{user.user.total_photos}</h3>
                  </div>
                  <div className='container3'>
                    <h2>{user.user.name}</h2>
                    <div className='new'>
                      <h3 className='thumbUp'>
                        <Fade icon={faThumbsUp} />
                      </h3>

                      <h3>{user.likes}</h3>
                    </div>

                    <p>{user.user.bio}</p>
                  </div>
                </div>
              </div>
            );
          })
        ) : (
          <h3>Loading...</h3>
        )}
      </div>
    );
  }
}
