import React, { useState } from 'react';
import '../../assets/main.css';
// import { statements } from '@babel/template';
import { useSpring, animated } from 'react-spring';
const Quotemachine = () => {
  const [state, setstate] = useState({
    title: 'Be yourself; everyone else is already taken.',
    author: 'Oscar Wilde'
  });

  const allowedState = [
    {
      title: 'So many books, so little time.',
      author: 'Frank Zappa'
    },
    {
      title: 'A room without books is like a body without a soul.',
      author: 'Marcus Tullius Cicero'
    },
    {
      title: 'You only live once, but if you do it right, once is enough.',
      author: 'Mae West'
    },
    {
      title:
        'Insanity is doing the same thing, over and over again, but expecting different results.',
      author: 'Narcotics Anonymous'
    },
    {
      title: 'Without music, life would be a mistake',
      author: 'Friedrich Nietzsche, Twilight of the Idols'
    },
    {
      title: 'We accept the love we think we deserve',
      author: 'Stephen Chbosky, The Perks of Being a Wallflower'
    }
  ];
  const moveBottom = useSpring({
    opacity: 1,
    marginTop: 0,
    from: { opacity: 0, marginTop: 500 },
    config: { duration: 1000 }
  });
  const items = useSpring({
    opacity: 1,
    from: { opacity: 0 },
    config: { duration: 2000 }
  });
  return (
    <animated.div style={moveBottom}>
      <div className='bg-green-200 h-screen pt-32'>
        <div className='bg-gray-200 w-2/3 mx-auto rounded p-16'>
          <animated.div style={items}>
            <h1 className='text-5xl text-red-500'>{state.title}</h1>
          </animated.div>
          <p className='text-2xl text-right text-blue-400'>{state.author}</p>
          <button
            className='bg-blue-500 hover:bg-blue-700 p-2 rounded-full text-white'
            onClick={() =>
              setstate(allowedState[Math.floor(Math.random() * 3)])
            }
          >
            New Quote
          </button>
        </div>
      </div>
    </animated.div>
  );
};

export default Quotemachine;
