import React, { useState, useEffect } from 'react';
import '../assets/main.css';
// import { useSpring, animated } from 'react-spring';
function useFetch(url) {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);
  async function fetchUrl() {
    const response = await fetch(url);
    const json = await response.json();
    setData(json);
    setLoading(false);
  }
  useEffect(() => {
    fetchUrl();
  }, []);
  return [data, loading];
}

export default function Github() {
  const [data, loading] = useFetch('https://api.github.com/users');
  return (
    <>
      <div className='mx-auto w-2/3'>
        <input
          type='text'
          className='shadow appearance-none border border-green-500 rounded  py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline w-2/3'
          placeholder='Add item'
        />
        <button className='bg-green-600 p-2 rounded ml-2 text-white hover:bg-green-300'>
          Add item
        </button>
        {/* <button>Random User</button> */}
      </div>
      {/* </div> */}
      {loading ? (
        'Loading...'
      ) : (
        <ul className='w-1/3'>
          {data.map(({ id, login, avatar_url, html_url }) => (
            <li key={id} className=''>
              <a href={html_url}>
                <img
                  alt={login}
                  src={avatar_url}
                  className='w-48 rounded-full m-auto'
                />
                <h3 className='text-center'>{login}</h3>
              </a>
            </li>
          ))}
        </ul>
      )}
    </>
  );
}
